import React  , {Component} from 'react'
import {Link} from 'react-router-dom'
import { FaLinkedinIn,FaTwitter,FaFacebookF,
    FaWhatsapp } from 'react-icons/fa'
   

import '../App.css'

export default class Footer extends Component {
    state = {
        isOpen : false,
    }

    handleToggle = () => {
        this.setState ({isOpen : !this.state.isOpen })
    }
    render(){
        return(
    <div className="footer">
        <div className="footertop">
            <div className="footerpara">
            
                <Link to="/" className="footerlogo ">
                    <p className="logopart1 ">RAINMAKER</p>
                        <p className="logopart2 ">Business Analytics</p>
                        </Link>
            
            
            </div>
           
            <div className="footerpara">
                <h6>Links</h6>
                <Link to="/"><p>Home</p></Link>
                <Link to="/Services"><p>Services</p></Link>
                <Link to="/establishanalytics"><p>Solutions</p></Link>
                <Link to="/Insights"><p>Insights</p></Link>
            
            </div>
             <div className="footerpara">
            
             <button type="button" className="getintch-button" 
                      onClick={this.handleToggle}>Get in touch with us</button>
                <p className="spacetopwrapper">sales@rainmakerba.com</p> 
               
               <div className="socialicons">
               <p>< FaTwitter  /></p>
               <p> < FaFacebookF /></p>
               <p> < FaLinkedinIn /></p>
               <p> < FaWhatsapp className="wup"/></p>
               
               </div>                
            </div>


    </div>
<hr className="linebot"  />
<div className="footerbottom">
<p className="copyright">&copy;RAINMAKERba {(new Date().getFullYear())}. All rights reserved</p> 
<p className="">Privacy policy</p> 
<p className="">Cookie policy</p> 
<p className="">Terms of use</p> 

</div>
</div>

)
        }
    }