import React from 'react'
import '../Styles/soldatacapture.css'
import '../App.css'

 const SolReporting =() => (
    <div className="datacapture">
         <hr className="linetop"  />
        <div className="datacapture1">
            <div className="datacapture11">
            <h2>
            Reporting to improve CEO/Executive decision-making process
            </h2>
            <p>
            The diffusion of the insights from the analytics program is crucial for 
            success. Insights and interesting data findings such as counterintuitive 
            findings need to be shared in interesting formats, and through the proper
            channel. Establishing streams of insights and data stories that are 
            minimalistic and easy-to-consume artifacts will increase acceptability and
            adoption. Rainmaker BA strongly believes such a report should replace 
            multi-page reports, dysfunctional and cross-linked spreadsheets, or 
            overloaded dashboards.
            </p>

            </div>
            <div className="datacapture11">
            <img src="assets\improveceodecision.jpg"
                    alt="blog" 
                    className="events-md-image" />
            </div>
        
            </div>
            <div className="datacapture2">
            <div className="datacapture21">
                <p>

                Rainmaker BA recommends ‘Data stories’ as an effective medium to present specific 
                aspects of the business, the performance of a specific product, the impact of certain
                decisions, and the results of large-scale online experiments. Data Stories enable 
                sharing of great insights to share if each ‘story’ is delivered in a smooth, 
                interactive, and engaging way. An ideal data story should be personalized, taking 
                into consideration the perspective of each target user (according to the role, 
                seniority, and line of business). 
                </p>
            </div>
            <div className="datacapture21">
                <p>

                Train Client employees in analytics.
                Having a cross-functional analytics team that encompasses functions of technology,
                business, operations, legal, and HR can help full-scale adoption of analytics in
                every department. Lack of teamwork among these cross-sections of departments can 
                hamper analytics-driven initiatives. Poor collaboration creates the risk that
                analytics may not get the relevant data and will not be able to provide the 
                information promised. This may result in distrust of the analytics program and 
                eventually to its abandonment.  Training of personnel in analytics concepts,
                practices and benefits can mitigate these risks. 
                </p>
            </div>
           
            
        </div>
        <div className="datacapture2">
           <div className="datacapture21">
           <p>
            There are many approaches to train and immerse employees in analytics. Rainmaker BA
            recommends empowering employees to easily access data and actionable insights as 
            paramount in analytics training. All key employees need to be aware of the available
            data, analytics metrics, and KPIs - for their department and the entire 
            organisation. A common understanding and interpretation of the Metrics and KPIs 
            related to analytics needs to be established across all levels of the organization. 
            This analytics dictionary must be accessible by all authorized users and packaged in 
            the right business context.
            </p>
           </div>
           <div className="datacapture21">
           <p>

            Employees need to be trained in technologies such as Artificial Intelligence,
            Analytics and Visualization. Awareness of these areas can be raised via several
            educational activities and events. Rainmaker BA conducts information sharing
            activities through group sessions. These sessions highlight success stories 
            where data led to smart decisions resulting in measurable financial or operational
            impact. Formal training sessions on data analysis technology is also a great tool
            to train employees into an analytical mindset. Conducting mini-hackathons and
            prediction competition on known data sets for employees are other approaches
            used to train employees.

            </p>
           </div>
       
       
       </div>

        </div>
)
export default SolReporting
